@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "open sans", sans-serif;
  font-size: 20px;
}

div.app {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
  color: #222;
  box-shadow: 0 0 10px 0 #ccc;
  max-width: 1300px;
}
@media (max-width: 1280px) {
  div.app {
    width: 100%;
  }
}
header {
  -webkit-flex-basis: 30vh;
          flex-basis: 30vh;
}

main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  padding: 30px 0;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

main aside {
  min-width: 240px;
}

main section.page {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

@media (max-width: 800px) {
  main {
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 30px 30px;
  }
}

footer {
  background-color: #000;
  color: #fff;
  padding: 10px 0;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
}

header {
  position: relative;
  overflow: hidden;
}

header img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

nav.main {
  padding: 0px 30px 30px 30px;
}

nav.main ul {
  list-style-type: none;
}

nav.main a {
  font-size: 0.9em;
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  color: #222;
  padding: 10px;
  transition: all 0.2s;
}

nav.main a:hover {
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
}

nav.main a.active {
  border-left: 5px solid grey;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

nav.main li:nth-child(1) a.active {
  border-color: green;
}

nav.main li:nth-child(2) a.active {
  border-color: blue;
}

nav.main li:nth-child(3) a.active {
  border-color: orange;
}

nav.main li:nth-child(4) a.active {
  border-color: red;
}

@media (max-width: 800px) {
  nav.main {
    padding: 0 0 30px 0;
  }
  nav.main li {
    display: inline-block;
  }

  nav.main a {
    border-bottom: 2px solid transparent;
    margin-right: 5px;
  }
  nav.main a:hover {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  nav.main a.active {
    border-left: none;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

.article-item {
  margin-bottom: 30px;
  padding: 20px;
  box-shadow: 0 0 3px 0 #ddd;
}

.article-title {
  text-transform: "uppercase";
  font-size: "1.0em";
}

.article-author {
  margin-bottom: 10px;
  display: "block";
  font-size: 0.6em;
}

.article-text {
  margin-top: 20px;
  font-size: 0.8em;
  text-align: justify;
}

@media (max-width: 800px) {
  .article-item {
    padding-right: 0;
  }
}

.product-title {
  font-weight: 700;
  font-size: 1.3em;
}
.item {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 40px 40px 40px 0;
  padding: 20px;
  font-size: 0.8em;
  box-shadow: 0 0 3px 0 #ddd;
}

.item .category {
  margin-bottom: 20px;
  font-size: 0.8em;
  color: #555;
}

.item .price {
  justify-self: center;
  font-weight: 700;
  color: #222;
}

.item .price span {
  font-size: 2em;
}

.item .image {
  padding: 2% 10%;
  width: 100%;
  max-width: 700px;
  text-align: center;
}

.item img {
  max-width: 100%;
  max-height: 100%;
}

.item .description {
  margin-bottom: 10px;
  padding: 10px 5% 10px 5%;
  font-size: 0.8em;
  word-break: break-all;
}

.item a {
  margin-right: 5%;
  margin-top: 10px;
  padding: 6px 12px;
  border-radius: 4px;
  background-color: #444;
  color: #fff;
  text-decoration: none;
  transition: 0.1s;
  -webkit-align-self: flex-end;
          align-self: flex-end;
}

.item a:hover {
  color: white;
  border-color: brown;
  background-color: brown;
}

@media (max-width: 800px) {
  .product-title {
    margin: 0 15px;
  }

  .item {
    margin: 40px 0;
  }
}

.product {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin-top: 40px;
  margin-right: 30px;
  padding: 20px;
  font-size: 0.8em;
  box-shadow: 0 0 3px 0 #ddd;
}

.product .title {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  margin-bottom: 20px;
}
.product .info {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  display: -webkit-flex;
  display: flex;
}
.product .description {
  /* flex-basis: 50%; */
  -webkit-flex-grow: 1;
          flex-grow: 1;
  margin-bottom: 10px;
  padding: 10px 10px 10px 0;
  font-size: 0.8em;
  word-break: break-all;
}

.product .image {
  padding-right: 30px;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  width: 30%;
  max-width: 300px;
  max-height: 150px;
  text-align: center;
}

.product img {
  max-width: 100%;
  max-height: 100%;
}

.product a {
  margin-right: 30px;
  margin-top: 10px;
  padding: 6px 12px;
  border-radius: 4px;
  background-color: #444;
  color: #fff;
  text-decoration: none;
  transition: 0.1s;
}

.product a:hover {
  color: white;
  background-color: brown;
}

@media (max-width: 800px) {
  .product {
    margin-top: 30px;
    margin-right: 0;
  }
}

.contact textarea {
  font-size: 16px;
  width: 80%;
  min-height: 200px;
  margin: 20px 0;
  line-height: 1.4;
  padding: 8px 10px;
  resize: vertical;
}

.contact button {
  display: block;
  margin: 0 20% 0 auto;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  padding: 10px 20px;
  background-color: #444;
  color: #fff;
  font-size: 20px;
  transition: 0.1s;
}

.contact button:hover {
  background-color: brown;
}

@media (max-width: 800px) {
  .contact textarea {
    width: 95%;
    min-height: 150px;
  }
  .contact button {
    margin: 0 5% 0 auto;
  }
}

.login {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-width: 400px;
  padding: 30px;
}

.login .user,
.login .password {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-bottom: 5px;
}

.login input {
  font-size: 0.8em;
  padding: 0 8px;
  line-height: 140%;
}

.login .button {
  display: block;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
  padding: 10px 20px;
  background-color: #444;
  color: #fff;
  font-size: 0.8em;
  transition: 0.1s;
}

.login .button:hover {
  background-color: brown;
}

@media (max-width: 800px) {
  .login {
    box-shadow: 0 0 6px 0 #ccc;
    margin: 0 auto;
    border-radius: 5px;
  }
}

.footer span {
  color: red;
}

.footer h2 {
  font-size: 14px;
}

.footer p {
  font-size: 12px;
}

